var outs = [23,43,76];
var ins = [54,73];
//
import Router from '../../../router/index'

export default {
	data () {
		return {
			navs: [
				{	
					icon: "#icon-wenzhang",
					path:'/add/upwork?n=1',
					num: ""
				},
				{
					icon: "#icon-sheying",
					path:'/add/upwork?n=2',
					num: ""
				},
				{
					icon: "#icon-biji",
					path:'/',
					num: ""
				},
			],
			innavs: [
				{
					name: "",
					num: ""
				},
				{
					name: "",
					num: ""
				},
			],
			teps: [	
				'',
				'',
				'',
				'',
				'',
				'',
				'',
				''
			],
			note: {
			backgroundImage: "url(" + require("../../images/logo2.svg") + ") ",
			backgroundPosition: "center center",
			backgroundRepeat: "no-repeat",
			backgroundSize: "cover",
        	},

		  outsum: 0,
		  insum: 0,
		  imgurl: '',
		  tep: '',
		}
	},
    computed:{
		
	},
	methods:{
		manage: function(){
			var _this = this;
			_this.$axios.get('http://127.0.0.1:4040/manage', {
			})
			.then(function (response) {
				var data = response.data;
				var tep = data.tep;
				if(tep == 0){
					Router.push({path: '/'});
				}else if(tep == 1){
					_this.imgurl = 'http://127.0.0.1:4040/user/user1.png';
					_this.name = data.myname;
				}
			})
			.catch(function (error) {
			    console.log(error);
			    alert(error);
			    Router.push({path: '/'});
			});
		},
		outdata: function(){
			var _this = this;
			for(var i=0;i<outs.length;i++){
				_this.navs[i].num = outs[i];
				_this.outsum += outs[i];
			}
		},
		indata: function(){
			var _this = this;
			for(var i=0;i<ins.length;i++){
				_this.innavs[i].num = ins[i];
				_this.insum += ins[i];
			}
		},
		random: function(){
			var _this = this;
			var i = 0; var j = _this.teps.length;
			var e = Math.floor(Math.random() * (j - i)) + i;
			_this.tep = _this.teps[e];
		},
		//
		createdb: function(index){
			var _this = this;
			if(index == 0 || index == 1){
				_this.$axios.post('http://127.0.0.1:4040/creatework', 
				    {num: index}
				)
				.then(function (response) {
					//var nowid = data.rest;
					Router.push({path: '/add/upwork?n='+index});
				})
				.catch(function (error) {
				    console.log(error);
				    alert(error)
				});			
			}else if(index == 2){
				Router.push({path: '/add/diary'});
			}
		}
	},
	mounted:function(){this.manage();this.outdata();this.indata();this.random()},

}


